import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import ButtonBase from "@material-ui/core/ButtonBase";
import instagram from "../Assets/instagram.png";
import Edited from "../Assets/Edited.png";
import AppStore from "../Assets/AppStore.png";
import PlayStore from "../Assets/PlayStore.png";
import Button from "@material-ui/core/Button";
import Dark from "../Assets/Dark.png";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(0),
    margin: "auto",
    maxWidth: "100%",
    backgroundColor: "#21212E",
    color: "#46C2FF",
  },
  image: {
    width: 50,
    height: 50,
  },
  img: {
    marginLeft: "50%",
    display: "flex",
    maxWidth: "50%",
    maxHeight: "100%",
  },
}));

export default function Two() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <Grid container>
          <Grid item>
            <Typography
              style={{
                paddingTop: 130,
                fontFamily: "Brandon Grotesque",
                color: "white",
              }}
            >
              <h1 >About Us</h1>
              <h2 style={{width:300}}>Mude Muse is new and at your service</h2>
              <p style={{width:300}}>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque,
                accusamus. Officiis, architecto expedita deleniti accusamus quos
                quo veniam eum sapiente ut ducimus, rem illum alias obcaecati
                facere molestiae corporis tempore.  
              </p>
            </Typography>
           
          </Grid>

          <Grid item sm container>
            <Grid item container direction="column" spacing={0}>
              <Grid item>
                <img className={classes.img} alt="complex" src={Dark} />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}
