import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';

import GridList1 from "../Assets/GridList1.png"
import GridList2 from "../Assets/GridList2.png"
import GridList3 from "../Assets/GridList3.png"
import GridList4 from "../Assets/GridList4.png"

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: "#21212E",
    padding:5
    
  },
  gridList: {
    flexWrap: 'nowrap',
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateZ(0)',
    
  },
  title: {
    color: theme.palette.primary.light,
  },
  titleBar: {
    background:
      'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
}));


  const tileData = [
    {
      img: GridList1,
      title: 'Image',
      author: 'author',
    },
    {
        img: GridList3,
        title: 'Image',
        author: 'author',
    },
    {
        img: GridList2,
        title: 'Image',
        author: 'author',
    },
    {
        img: GridList4,
        title: 'Image',
        author: 'author',
    },
    {
        img: GridList1,
        title: 'Image',
        author: 'author',
    },
  ];
 
export default function Three() {
  const classes = useStyles();

  return (
      <>
      <h1 style={{fontFamily:"Brandon Grotesque",color:"white",padding:5}}>Showcase</h1>
    <div className={classes.root}>
        
      <GridList className={classes.gridList} cols={3.5}>
        {tileData.map((tile) => (
          <GridListTile key={tile.img} style={{height:500}}>
            <img src={tile.img} alt={tile.title} />
            <GridListTileBar
              title={tile.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              actionIcon={
                <IconButton aria-label={`star ${tile.title}`}>
                  <StarBorderIcon className={classes.title} />
                </IconButton>
              }
            />
          </GridListTile>
        ))}
      </GridList>
    </div>
    </>
  );
}
