import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import instagram from "../Assets/instagram.png"
import Edited from "../Assets/Edited.png"
import AppStore from "../Assets/AppStore.png"
import PlayStore from "../Assets/PlayStore.png"
import Button from "@material-ui/core/Button"


const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(0),
      margin: 'auto',
      maxWidth: "100%",
      backgroundColor:"#21212E",
      color:"#46C2FF"
    },
    image: {
      width: 50,
      height: 50,
    },
    img: {
      marginLeft: '30%',
      display: 'flex',
      maxWidth: '50%',
      maxHeight: '100%',
    },
  }));

export default function One(){
    const classes = useStyles();

    return(
<div className={classes.root}>
      <Paper className={classes.paper}>
        <Grid container spacing={0}>
          <Grid item>
          <Typography style={{paddingTop:90,
          fontFamily:"Brandon Grotesque",
          color:"white"

        }}>
                  <h1>Music To Your Ears</h1>
                  <p>A mobile app is computer program designed to run on a mobile device
                      <br/>
                      such as phone tablet or watch
                  </p>
                </Typography>
                <ButtonBase variant="disabled"  >
                    <img src={AppStore} alt="" style={{width:200}}/>
                    <img src={PlayStore} alt="" style={{width:200}}/>
                </ButtonBase>
               
              </Grid>
             
          <Grid item  sm container>
            <Grid item  container direction="column" spacing={2}>
              <Grid item >
                  
              
              <img className={classes.img} alt="complex" src={Edited}/>
            
              </Grid>
            </Grid>
            
          </Grid>
        </Grid>
      </Paper>
    </div>
    )
}